<template>
  <div id="home">
    <nav-bar >
      <template #center >
        
      </template>
    </nav-bar>
    <tab-control
      :title="['','','']"
      @ChangeIndex="ChangeIndex"
      ref="tabControl2"
      class="fixed"
      v-show="isTabTop"/>
    <scroll class="content"
            ref="scroll"
            :probe-type="3"
            @scroll="contentScroll"
            @pullUp="pullUp">
        <home-swiper
          :banner="banner"
          @swiperImgLoad="swiperImgLoad"/>
        <recommend-view :recommend="recommend"/>
        <feature-view />
        <tab-control
          :title="['','','']"
          @ChangeIndex="ChangeIndex"
          ref="tabControl1" />
        <goods-list :goods="goodsShow" />
    </scroll>
    <go-back-top  @click.native="backTop" v-show="isShowBackTop"/>
  </div>
</template>

<script>
  import {debounce} from 'common/utils'
  import {imgLoadMixin} from 'common/mixin'

  import NavBar from 'components/common/navbar/NavBar'
  import TabControl from 'components/content/tabControl/TabControl'
  import GoodsList from 'components/content/goods/GoodsList'
  import Scroll from 'components/common/scroll/Scroll'
  import GoBackTop from 'components/content/goBackTop/GoBackTop'

  import HomeSwiper from './childComps/HomeSwiper'
  import RecommendView from './childComps/RecommendView'
  import FeatureView from './childComps/FeatureView'

  import {getHomeMultidata,getHomeGoods} from 'network/home'

  export default {
    name: "Home",
    data () {
      return {
        banner : [],
        recommend : [],
        goods : {
          pop: {
            type: 'pop',
            page: 0,
            list: []
          },
          new: {
            type: 'new',
            page: 0,
            list: []
          },
          sell: {
            type: 'sell',
            page: 0,
            list: []
          }
        },
        show: 'pop',
        isShowBackTop: false,
        offsetTop: 0,
        isTabTop: false,
        scrollY: 0,
      }
    },
    components: {
      NavBar,
      HomeSwiper,
      RecommendView,
      FeatureView,
      TabControl,
      GoodsList,
      Scroll,
      GoBackTop
    },
    mixins: [imgLoadMixin],
    created() {
      //  
      this.getHomeMultidata()
      //
      this.getHomeGoods(this.goods.pop.type)
      this.getHomeGoods(this.goods.new.type)
      this.getHomeGoods(this.goods.sell.type)
    },
    mounted() {
      /*
      //Scrollrefresh
      const fresh = debounce(this.$refs.scroll.refresh,100)

     //$on
      this.$bus.$on('HomeLoadImg',() => {
        //fresh
        fresh()
      }) */
    },
    activated(){
      //
      this.$refs.scroll.refresh()
      //
      this.$refs.scroll.scrollTo(0,this.scrollY,0)
    },
    deactivated(){
      //
      this.scrollY = this.$refs.scroll.getScroll()

      //LoadImg
      this.$bus.$off('LoadImg',this.loadImg)
    },
    computed: {
      goodsShow() {
        return this.goods[this.show].list
      }
    },
    methods: {
      //
      //
      getHomeMultidata() {
        getHomeMultidata().then(res => {
          this.banner = res.data.banner.list
          this.recommend = res.data.recommend.list
        })
      },
      //
      getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type,page).then(res => {
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page += 1

          //scroll
          this.$refs.scroll.finishPullUp()
        })
      },

      //

      //
      ChangeIndex(index) {
        switch (index) {
          case 0: this.show = 'pop'
            break
          case 1: this.show = 'new'
            break
          case 2: this.show = 'sell'
        }
        //tab-controlIndex
        this.$refs.tabControl1.currentIndex = index
        this.$refs.tabControl2.currentIndex = index
      },
      //
      backTop() {
        //
        this.$refs.scroll.scrollTo(0,0,200)
        //
        // this.$refs.scroll.scroll.scrollTo(0,0,500)
      },

      //
      contentScroll(position) {
        //
        this.isShowBackTop = -position.y > 1000
        //isTabTop
        this.isTabTop = -position.y > this.offsetTop
      },

      //
      pullUp() {
        this.getHomeGoods(this.show)
      },
      //tab-control offsetTop
      swiperImgLoad() {
        this.offsetTop = this.$refs.tabControl1.$el.offsetTop
      }
    }
  }
</script>

<style scoped>
  #home {
    position: relative;
    height: 100vh;
  }
  .nav-bar {
    background: var(--color-tint);
    color: #fff;
  }
  .fixed {
    position: fixed;
    top: 44px;
    left: 0;
    right: 0;
    z-index: 2;
  }
  .content {
    position: absolute;
    top: 44px;
    bottom: 48px;
    overflow: hidden;
    z-index: 1;
  }
</style>
